import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';

const DynamicChart = props => {
  const [chartData, setChartData] = useState({});
  const [employeeSalary, setEmployeeSalary] = useState([]);
  const [employeeAge, setEmployeeAge] = useState([]);

  console.log(props.covidStats.history[400].deaths);
  const covidData = props.covidStats;

  const Chart = () => {
    setChartData({
      labels: [
        covidData.history[394].date.slice(0, 10),
        covidData.history[395].date.slice(0, 10),
        covidData.history[396].date.slice(0, 10),
        covidData.history[397].date.slice(0, 10),
        covidData.history[398].date.slice(0, 10),
        covidData.history[399].date.slice(0, 10),
        covidData.history[400].date.slice(0, 10),
      ],
      datasets: [
        {
          label: 'Deaths',
          data: [
            props.covidStats.history[394].deaths,
            props.covidStats.history[395].deaths,
            props.covidStats.history[396].deaths,
            props.covidStats.history[397].deaths,
            props.covidStats.history[398].deaths,
            props.covidStats.history[399].deaths,
            props.covidStats.history[400].deaths,
          ],
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)',
          ],
          borderColor: [
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)',
          ],
          borderWidth: 1,
        },
      ],
    });
  };
  useEffect(() => {
    Chart();
  }, []);

  return (
    <div className='App'>
      <h1>Bar Chart</h1>
      <div>
        <Bar
          data={chartData}
          options={{
            responsive: true,
            title: { text: 'THICCNESS SCALE', display: true },
            scales: {
              yAxes: [
                {
                  ticks: {
                    beginAtZero: false,
                  },
                },
              ],
            },
          }}
        />
      </div>
    </div>
  );
};

export default DynamicChart;
